
<template>
  <div class="Component-PaginaExportar">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Exportar</h3>
    <div>
      <ul class="una_lista_de_opciones_seguidas">
        <li class="" :class="{ seleccionado: internal_selected_option === 'monitoreo' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'monitoreo')">Monitoreo</a>
        </li>
        <li class="un_separador">|</li>
        <li class="" :class="{ seleccionado: internal_selected_option === 'configuraciones' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'configuraciones')">Configuraciones</a>
        </li>
      </ul>
    </div>

    <div v-if="internal_selected_option">
      <h3 v-if="internal_selected_option === 'monitoreo'" class="texto_importante_1">Monitoreo</h3>
      <h3 v-if="internal_selected_option === 'configuraciones'" class="texto_importante_1">Configuraciones</h3>

      <ul class="una_lista_de_opciones_seguidas">
        <li class="" :class="{ seleccionado: internal_selected_option_2 === 'a fichero' }">
          <a href="#" v-on:click="() => (internal_selected_option_2 = 'a fichero')">A fichero</a>
        </li>
        <li class="un_separador">|</li>
        <li class="" :class="{ seleccionado: internal_selected_option_2 === 'a texto' }">
          <a href="#" v-on:click="() => (internal_selected_option_2 = 'a texto')">A texto</a>
        </li>
      </ul>

      <div>
        <h3 v-if="internal_selected_option_2 === 'a texto'" class="texto_importante_1">A texto</h3>
        <h3 v-if="internal_selected_option_2 === 'a fichero'" class="texto_importante_1">A fichero</h3>

        <div v-if="internal_selected_option === 'monitoreo' && internal_selected_option_2 === 'a texto'">
          <div class="un_espaciado">
            <button class="un_control" v-on:click="() => exportDatabaseToText()">Exportar monitoreo a texto</button>
          </div>
          <div class="un_espaciado_sin_arriba">
            <textarea class="un_control" v-model="internal_exported_monitoreo_text" placeholder="Aquí se mostrará la exportación del monitoreo a texto en formato JSON."></textarea>
          </div>
        </div>
        <div v-else-if="internal_selected_option === 'monitoreo' && internal_selected_option_2 === 'a fichero'">
          <div class="un_espaciado">
            <button class="un_control" v-on:click="() => exportDatabaseToFile()">Exportar monitoreo a fichero</button>
          </div>
        </div>
        <div v-else-if="internal_selected_option === 'configuraciones' && internal_selected_option_2 === 'a texto'">
          <div class="un_espaciado">
            <button class="un_control" v-on:click="() => exportPersonalDataToText()">Exportar configuraciones a texto</button>
          </div>
          <div class="un_espaciado_sin_arriba">
            <textarea class="un_control" v-model="internal_exported_personal_data_text" placeholder="Aquí se mostrará la exportación de las configuraciones a texto en formato JSON."></textarea>
          </div>
        </div>
        <div v-else-if="internal_selected_option === 'configuraciones' && internal_selected_option_2 === 'a fichero'">
          <div class="un_espaciado">
            <button class="un_control" v-on:click="() => exportPersonalDataToFile()">Exportar configuraciones a fichero</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginaExportar",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    const storedSettings = this.$db.getSettings();
    return {
      internal_project_name: storedSettings.project_name || "",
      internal_selected_option: undefined,
      internal_selected_option_2: undefined,
      internal_exported_monitoreo_text: undefined,
      internal_exported_personal_data_text: undefined,
    };
  },
  methods: {
    exportDatabaseToFile() {
      const allDatabase = this.$db.getDatabase();
      const allSettings = this.$db.getSettings();
      const projectId = allSettings.project_id || "Body and mind control";
      this.$utils.downloadLocalFile(projectId + "." + this.$utils.getDateForFiles(new Date()) + ".mbc-database.json", allDatabase);
    },
    exportPersonalDataToFile() {
      const allSettings = this.$db.getSettings();
      const username = allSettings.username || "Pepito Yons Smidsonian";
      this.$utils.downloadLocalFile(username + "." + this.$utils.getDateForFiles(new Date()) + ".mbc-personal.json", allSettings);
    },
    exportDatabaseToText() {
      const allDatabase = this.$db.getDatabase();
      this.internal_exported_monitoreo_text = JSON.stringify(allDatabase);
    },
    exportPersonalDataToText() {
      const allSettings = this.$db.getSettings();
      this.internal_exported_personal_data_text = JSON.stringify(allSettings);
    },
  },
  watch: {
    internal_project_name(v) {
      this.$db.setSettings({ project_name: v });
    },
  },
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
};
</script>
<style>
.Component-PaginaExportar {
}
</style>
