
<template>
  <div class="Component-PaginaImportar">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Importar</h3>
    <div>
      <ul class="una_lista_de_opciones_seguidas">
        <li class="" :class="{ seleccionado: internal_selected_option === 'monitoreo' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'monitoreo')">Monitoreo</a>
        </li>
        <li class="un_separador">|</li>
        <li class="" :class="{ seleccionado: internal_selected_option === 'configuraciones' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'configuraciones')">Configuraciones</a>
        </li>
      </ul>
    </div>

    <div v-if="internal_selected_option">
      <h3 v-if="internal_selected_option === 'monitoreo'" class="texto_importante_1">Monitoreo</h3>
      <h3 v-if="internal_selected_option === 'configuraciones'" class="texto_importante_1">Configuraciones</h3>

      <ul class="una_lista_de_opciones_seguidas">
        <li class="" :class="{ seleccionado: internal_selected_option_2 === 'desde texto' }">
          <a href="#" v-on:click="() => (internal_selected_option_2 = 'desde texto')">Desde texto</a>
        </li>
      </ul>

      <div v-if="internal_selected_option && internal_selected_option_2">
        <h3 v-if="internal_selected_option_2 === 'desde texto'" class="texto_importante_1">Desde texto</h3>
        <h3 v-if="internal_selected_option_2 === 'desde fichero'" class="texto_importante_1">Desde fichero</h3>

        <div v-if="internal_selected_option === 'monitoreo' && internal_selected_option_2 === 'desde texto'">
          <div class="un_espaciado_sin_abajo">
            <div v-if="internal_monitoreo_error">
              <ErrorDePagina :root="root" error-id="import-monitoreo" :error="internal_monitoreo_error" :on-change="(v) => (internal_monitoreo_error = v)" />
            </div>
            <textarea class="un_control" placeholder="Aquí se inserta la importación del «MONITOREO» desde texto en formato JSON." v-model="internal_monitoreo_text"></textarea>
            <div class="un_espaciado_arriba"></div>
            <button class="un_control" v-on:click="() => importDatabaseFromText()">Importar monitoreo desde texto</button>
          </div>
        </div>

        <div v-else-if="internal_selected_option === 'configuraciones' && internal_selected_option_2 === 'desde texto'">
          <div class="un_espaciado_sin_abajo">
            <div v-if="internal_personal_data_error">
              <ErrorDePagina :root="root" error-id="import-personal-data" :error="internal_personal_data_error" :on-change="(v) => (internal_personal_data_error = v)" />
            </div>
            <textarea class="un_control" placeholder="Aquí se inserta la importación de las «CONFIGURACIONES» desde texto en formato JSON." v-model="internal_personal_data_text"></textarea>
            <div class="un_espaciado_arriba"></div>
            <button class="un_control" v-on:click="() => importSettingsFromText()">Importar configuraciones desde texto</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginaImportar",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      internal_selected_option: undefined,
      internal_selected_option_2: "desde texto",
      internal_selected_to_text_section: undefined,
      internal_monitoreo_text: undefined,
      internal_monitoreo_error: undefined,
      internal_personal_data_text: undefined,
      internal_personal_data_error: undefined,
    };
  },
  methods: {
    selectToTextSection(section) {
      this.internal_selected_to_text_section = section;
    },
    importDatabaseFromText() {
      try {
        const isConfirmed = window.confirm(`¡CUIDADO, ${this.$db.getSettings().username || "Pepito Yons Smidsonian"}!\n¿Acepta PERDER todos los DATOS DE MONITOREO anteriores?\nEsto es: actividades, tareas, comidas...`);
        if (!isConfirmed) {
          return;
        }
        if (typeof this.internal_monitoreo_text !== "string") {
          this.internal_monitoreo_text = "";
        }
        const allDatabase = JSON.parse(this.internal_monitoreo_text);
        this.$db.setDatabase(allDatabase);
      } catch (error) {
        console.error(error);
        this.internal_monitoreo_error = error;
      }
    },
    importSettingsFromText() {
      try {
        const isConfirmed = window.confirm(`¡CUIDADO, ${this.$db.getSettings().username || "Pepito Yons Smidsonian"}!\n¿Acepta PERDER todas las CONFIGURACIONES anteriores?\nEsto es: nombre de usuario, de proyecto...`);
        if (!isConfirmed) {
          return;
        }
        if (!isConfirmed) {
          return;
        }
        if (typeof this.internal_personal_data_text !== "string") {
          this.internal_personal_data_text = "";
        }
        const allSettings = JSON.parse(this.internal_personal_data_text);
        this.$db.setSettings(allSettings);
      } catch (error) {
        console.error(error);
        this.internal_personal_data_error = error;
      }
    },
  },
  watch: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
};
</script>
