<template>
  <div class="Component-ControlDeLista">
    <label class="una_etiqueta">{{ internal_label }}</label>
    <table class="tabla_sin_bordes ancho_maximo">
      <tbody class="">
        <tr class="" v-for="(value, indexValue) in internal_value" v-bind:key="'control-de-lista-' + uid + '-row-' + indexValue">
          <td class="" style="width: 100%">
            <div class="un_espaciado_abajo">
              <select class="un_control" v-model="internal_value[indexValue][internal_option_id]">
                <option v-for="(option, optionIndex) in internal_options" :value="option[internal_option_id]" v-bind:key="'control-de-lista-' + uid + '-option-' + optionIndex">{{ option[internal_option_id] }}</option>
              </select>
            </div>
          </td>
          <td class="">
            <div class="un_espaciado_abajo">
              <button class="un_control" v-on:click="deleteItem(indexValue)">Eliminar</button>
            </div>
          </td>
        </tr>
        <tr class="">
          <td class="" colspan="10">
            <button class="un_control" v-on:click="addItem()">Añadir {{ internal_model }}</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: "ControlDeLista",
  props: {
    root: {
      type: Object,
      required: true,
    },
    label: {
      type: String,
      required: false,
    },
    value: {
      type: Array,
      default: () => [],
    },
    model: {
      type: String,
      required: true,
    },
    options: {
      type: Array,
      required: false,
    },
    optionId: {
      type: String,
      default: () => "nombre",
    },
    selectedOption: {
      type: Number,
      default: () => -1,
    },
    onChange: {
      type: Function,
      required: false,
      default: () => () => undefined,
    },
    uid: {
      type: String,
      default: function () {
        return this.$utils.getRandomString(10);
      },
    },
  },
  data() {
    return {
      internal_label: this.label,
      internal_value: this.value,
      internal_model: this.model,
      internal_options: this.options,
      internal_option_selected: this.selectedOption,
      internal_option_id: this.optionId,
      internal_on_change: this.onChange,
    };
  },
  methods: {
    objectToString(o) {
      return Object.keys(o).reduce((output, key, index) => {
        if (index !== 0) {
          output += " | ";
        }
        output += `${this.$utils.fromCamelToCapital(key)}: ${o[key]}`;
        return output;
      }, "");
    },
    addItem() {
      this.internal_value.push({});
    },
    deleteItem(indexItem) {
      this.internal_value.splice(indexItem, 1);
    },
  },
  watch: {
    internal_option_selected(v) {
      this.internal_value = this.internal_options[v][this.internal_option_id];
    },
    internal_value(v) {
      this.internal_value_as_string = v ? v[this.option_id] : undefined;
      this.internal_on_change(v, this);
    },
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_on_change(this.internal_value, this);
  },
  updated() {},
  destroyed() {},
};
</script>
