<template>
  <div class="Component-ControlDeEntero">
    <label class="una_etiqueta">{{ internal_label }}</label>
    <input class="un_control" type="integer" :placeholder="internal_placeholder" v-model="internal_value" />
  </div>
</template>
<script>

export default {
    name: "ControlDeEntero",
    props: {
        root: {
            type: Object,
            required: true,
        },
        label: {
            type: String,
            required: false,
        },
        value: {
            type: String,
            required: false,
        },
        placeholder: {
            type: String,
            required: false,
        },
        onChange: {
            type: Function,
            required: false,
            default: () => () => undefined
        },
    },
    data() {
        return {
            internal_label: this.label,
            internal_value: this.value,
            internal_placeholder: this.placeholder,
            internal_on_change: this.onChange,
        };
    },
    methods: {},
    watch: {
        internal_value(v) {
            this.internal_on_change(v, this);
        }
    },
    computed: {},
    beforeCreate() {},
    beforeMount() {},
    beforeUpdate() {},
    beforeDestroy() {},
    created() {},
    mounted() {
        this.internal_on_change(this.internal_value, this);
    },
    updated() {},
    destroyed() {},
};

</script>
