<template>
  <div class="Component-FormularioDeModelo">
    <div class="" v-if="internal_page_error">
      <ErrorDePagina :root="root" :error="internal_page_error" :on-clear-error="x => internal_page_error = undefined" />
    </div>
    <div class="un_espaciado" v-if="is_loading_operation">
      <div class="el_mensaje_de_carga">
        Efectuando operación... Por favor, espere...
      </div>
    </div>
    <div class="un_espaciado" v-else-if="typeof internal_value === 'undefined'">
      <div class="el_mensaje_de_carga">
        Cargando formulario... Por favor, espere...
      </div>
    </div>
    <div class="un_espaciado" v-else-if="internal_value">
      <span class="un_texto_pequenio un_subrayado">Formulario de: <b>{{ $utils.fromCamelToCapital(internal_model) }}</b></span>
      <div class="un_espaciado_arriba un_separador_de_control">
        <div v-if="internal_mode === 'editar'">
          <button class="un_control" v-on:click="saveItem" >Guardar {{ internal_model }}</button>
        </div>
        <div v-else-if="internal_mode === 'aniadir'">
          <button class="un_control" v-on:click="addItem">Crear {{ $utils.fromCamelToCapital(internal_model).toLowerCase() }}</button>
        </div>
      </div>
      <div class="una_columna_del_formulario" v-for="(columnData, columnId) in internal_model_schema.columnas" v-bind:key="'form-' + internal_model + '-column-' + columnId">
        <div class="un_espaciado_arriba un_separador_de_control" v-if="columnData.startsWith('prototype:') && (columnId in available_options_for) && typeof available_options_for[columnId] === 'object'">
            <ControlDeSelector :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" placeholder="" :value="internal_value[columnId]" :on-change="v => setAsPrototype(v)" :options="available_options_for[columnId]" :value-column="undefined" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-if="columnData === 'string'">
            <ControlDeTexto :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" placeholder="" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-if="columnData === 'text'">
            <ControlDeTextoLargo :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" placeholder="" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-else-if="columnData === 'date'">
            <ControlDeFecha :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-else-if="columnData === 'integer'">
            <ControlDeEntero :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-else-if="columnData === 'binary'">
            <ControlDeBinario :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" />
        </div>
        <div class="un_espaciado_arriba un_separador_de_control" v-else-if="columnData.startsWith('list:') && (columnId in available_options_for) && typeof available_options_for[columnId] === 'object'">
            <ControlDeLista :root="root" :label="$utils.capitalizeAndHumanize(columnId) + ':'" :value="internal_value[columnId]" :on-change="v => internal_value[columnId] = v" :options="available_options_for[columnId]" :model="columnData.replace('list:', '')" />
        </div>
      </div>
      <div class="un_espaciado_arriba un_separador_de_control">
        <div v-if="internal_mode === 'editar'">
          <button class="un_control" v-on:click="saveItem" >Guardar {{ internal_model }}</button>
        </div>
        <div v-else-if="internal_mode === 'aniadir'">
          <button class="un_control" v-on:click="addItem">Crear {{ $utils.fromCamelToCapital(internal_model).toLowerCase() }}</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "FormularioDeModelo",
  props: {
    root: {
      type: Object,
      required: true,
    },
    model: {
      type: String,
      required: true,
    },
    mode: {
      type: String,
      default: () => "aniadir",
    },
    value: {
      type: Object,
      default: () => ({}),
    }
  },
  data() {
    return {
      is_loading_operation: false,
      internal_value: this.value,
      internal_mode: this.mode,
      internal_model: this.model,
      internal_model_schema: this.$db.schema.modelos[this.model],
      internal_page_error: undefined,
      available_options_for: {},
    };
  },
  methods: {
    addItem() {
      this.is_loading_operation = true;
      const value = Object.assign({}, this.internal_value);
      this.$db.insert(this.internal_model, value)
        .then(data => {
          this.is_loading_operation = false;
          this.$noop(data);
          this.root.goBack();
          return data;
        })
        .catch(error => {
          this.is_loading_operation = false;
          console.error(error, this.internal_value.value);
          return this.internal_page_error = error;
        });
    },
    saveItem() {
      this.is_loading_operation = true;
      const value = Object.assign({}, this.internal_value);
      this.$db.update(this.internal_model, value.id, value)
        .then(data => {
          this.is_loading_operation = false;
          this.$noop(data);
          this.root.goBack();
          return data;
        })
        .catch(error => {
          this.is_loading_operation = false;
          console.error(error, this.internal_value.value);
          return this.internal_page_error = error;
        });
    },
    setValue(v) {
      this.internal_value = v;
    },
    setAsPrototype(v) {
      if(typeof v !== "object") {
        return;
      }
      if(v === null) {
        return;
      }
      if(Object.keys(v).length === 0) {
        return;
      }
      const v2 = Object.assign({}, v);
      delete v2["fecha de inicio"];
      delete v2.id;

      if(v2.nombre) {
        const consonants = "BCDFGJKLMNPRSTVXZ".split("");
        const vocals = "AEIOU".split("");
        const numbers = "0123456789".split("");
        // const alphabet = "ABCDEFGHIJKLMNOPRSTUVWXYZ".split("");
        const uid = ""
          + this.$utils.getRandomString(1, consonants)
          + this.$utils.getRandomString(1, vocals)
          + this.$utils.getRandomString(1, consonants)
          + ""
          + this.$utils.getRandomString(1, numbers)
          + "-"
          + this.$utils.getRandomString(1, consonants)
          + this.$utils.getRandomString(1, vocals)
          + this.$utils.getRandomString(1, consonants)
          + ""
          + this.$utils.getRandomString(1, numbers)
          + "-"
          + this.$utils.getRandomString(1, consonants)
          + this.$utils.getRandomString(1, vocals)
          + this.$utils.getRandomString(1, consonants)
          + ""
          + this.$utils.getRandomString(1, numbers)
          + "-"
          + this.$utils.getRandomString(1, consonants)
          + this.$utils.getRandomString(1, vocals)
          + this.$utils.getRandomString(1, consonants)
          + ""
          + this.$utils.getRandomString(1, numbers);
        v2.nombre = v2.nombre + " [" + uid + "]";
        // v2.nombre = v2.nombre + " [" + this.$utils.getRandomString(20,"0123456789".split("")) + "]";
      }
      //console.log(v2, this.internal_value);
      this.is_loading_operation = true;
      this.internal_value = Object.assign({}, this.internal_value, v2);
      setTimeout(() => this.is_loading_operation = false, 0);
    },
    refreshAvailableOptions() {
      const allColumns = this.internal_model_schema.columnas;
      const allColumnIds = Object.keys(allColumns);
      IteratingColumns:
      for(let index = 0; index < allColumnIds.length; index++) {
        const columnId = allColumnIds[index];
        const columnData = allColumns[columnId];
        const isList = columnData.startsWith("list:");
        const isPrototype = columnData.startsWith("prototype:");
        if(isList) {
          this.$db.select(columnData.replace("list:", ""))
          .then(data => {
            this.available_options_for[columnId] = data;
            this.$forceUpdate(true);
          })
          .catch(error => {
            console.error(error, this.internal_value.value);
            this.internal_page_error = error;
          });
        } else if(isPrototype) {
          this.$db.select(columnData.replace("prototype:", ""))
          .then(data => {
            this.available_options_for[columnId] = data;
            this.$forceUpdate(true);
          })
          .catch(error => {
            console.error(error, this.internal_value.value);
            this.internal_page_error = error;
          });
        }
        continue IteratingColumns;
      }
    }
  },
  watch: {},
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.setValue(this.value);
    this.refreshAvailableOptions();
  },
  updated() {},
  destroyed() {},
};
</script>
<style>
.Component-FormularioDeModelo {
  text-align: left;
}
</style>