        </div>
        <div class="como_una_celda un_espaciado">
          <button class="un_control una_anchura_de_20_px" v-on:click="() => goToNextPanel()">»</button>
        </div>
        <div class="como_una_celda un_espaciado" style="padding-left: 0px;">
          <button class="un_control una_anchura_de_20_px" v-on:click="() => internal_selected_main_panel = !internal_selected_main_panel">{{ internal_selected_main_panel ? "-" : "+" }}</button>
        </div>
      </div>
      <div class="un_espaciado_sin_arriba" style="margin-top: 1px" v-if="internal_selected_main_panel">
        <div class="una_carta_informativa">
          <IntroductorioPorHora :root="root" v-if="internal_selected_panel === 'Ver hora'" />
          <IntroductorioPorDia :root="root" v-else-if="internal_selected_panel === 'Ver día'" />
          <IntroductorioPorSemana :root="root" v-else-if="internal_selected_panel === 'Ver semana'" />
          <IntroductorioPorMes :root="root" v-else-if="internal_selected_panel === 'Ver mes'" />
          <IntroductorioPorAnio :root="root" v-else-if="internal_selected_panel === 'Ver año'" />
          <IntroductorioPorBalance :root="root" v-else-if="internal_selected_panel === 'Ver balance'" />
