</template>
<script>
export default {
  name: "PaginaHome",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      internal_selected_view: undefined,
      internal_selected_panel: "Ver hora",
      internal_selected_main_panel: true,
      available_ids: ["Agenda", "Alimentación", "Actividad", "Listas", "Notas", "Esquemas", "Guía de ayuda", "Importar", "Exportar", "Consola", "Scripts", "Configuración"],
      available_panels: ["Ver hora", "Ver día", "Ver mes", "Ver año", /*"Ver balance"*/],
    };
  },
  methods: {
    goToPreviousPanel() {
      const currentPos = this.available_panels.indexOf(this.internal_selected_panel);
      const previousPos = (currentPos === 0) ? (this.available_panels.length-1) : (currentPos-1);
      this.$refs.selector_de_panel_principal.internal_value = this.available_panels[previousPos];
      this.internal_selected_main_panel = true;
    },
    goToNextPanel() {
      const currentPos = this.available_panels.indexOf(this.internal_selected_panel);
      const nextPos = (currentPos === this.available_panels.length-1) ? (0) : (currentPos+1);
      this.$refs.selector_de_panel_principal.internal_value = this.available_panels[nextPos];
      this.internal_selected_main_panel = true;
    },
  },
  watch: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    
  },
  updated() {},
  destroyed() {},
};
</script>
