
<template>
  <div class="Component-PaginaAgendaVerAgenda">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Día por día</h3>
    <div class="un_mensaje_de_carga un_espaciado" v-if="!internal_target_date">Cargando día... Por favor, espere...</div>
    <div class="" v-else>
      <div class="">
        <table class="una_tabla tabla_sin_bordes">
          <tbody>
            <tr>
              <td class="un_espaciado">
                <a class="" href="#" v-on:click="() => goToPreviousDay()">
                  <button class="un_control una_anchura_de_20_px" v-on:click="goToPreviousDay">«</button>
                </a>
              </td>
              <td class="un_separador"></td>
              <td class="el_texto_del_dia_de_la_agenda">
                Día <span style="text-transform: none !important">{{ $utils.getPoliteStringFromDate(internal_target_date, { resetFromHours: true }) }}</span>
              </td>
              <td class="un_separador"></td>
              <td class="un_espaciado">
                <a href="#" v-on:click="() => goToNextDay()">
                  <button class="un_control una_anchura_de_20_px" v-on:click="goToNextDay">»</button>
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="un_espaciado" style="padding-top: 0px">
        <select class="un_control" v-model="internal_selected_option">
          <option value="por tareas">Por tareas</option>
          <option value="por horas">Por horas</option>
        </select>
      </div>
      <div class="un_espaciado_sin_arriba" v-if="internal_selected_option === 'por tareas'" v-bind:key="'agenda-vista-por-tareas'">
        <!--h3 class="texto_importante_1">por tareas</h3-->
        <div class="">
          <div class="un_texto_pequenio" v-if="typeof internal_tareas_per_tarea === 'undefined'">Cargando tareas del día... Por favor, espere...</div>
          <div class="un_texto_pequenio" v-else-if="typeof internal_tareas_per_tarea === 'object' && Object.keys(internal_tareas_per_tarea).length === 0">No se encontraron tareas para este día. Cree una nueva tarea <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => goToAddTarea()">aquí</a>.</div>
          <table class="una_tabla la_tabla_de_la_agenda" v-else>
            <tbody v-for="(tareaDetails, tareaIndex) in internal_tareas_per_tarea" v-bind:key="'agenda-vista-por-horas-tarea-' + tareaIndex">
              <tr>
                <td class="ancho_maximo">
                  <h3 class="texto_importante_1" :class="{ una_tarea_hecha: tareaDetails['está terminada'] }">
                    <a href="javascript:void(0)" v-on:click="() => (tareaDetails['está terminada'] ? undoTarea(tareaDetails.id) : doTarea(tareaDetails.id))">
                      <span v-if="tareaDetails['está terminada']" style="white-space: nowrap">✔ </span>
                      <span v-else style="white-space: nowrap">✖ </span>
                    </a>
                    <span style="font-size: 8px; vertical-align: middle; margin-right: 5px" v-if="tareaDetails['fecha de inicio']">{{ $utils.formatHourAmPmFromString(tareaDetails["fecha de inicio"]) }}</span>
                    <span>{{ tareaDetails["está terminada"] ? "(Hecho)" : "" }} {{ tareaDetails.nombre }}</span>
                  </h3>
                </td>
              </tr>
              <tr>
                <td class="ancho_maximo">
                  <ul class="una_lista_seguida la_lista_de_tareas_de_una_hora_de_la_agenda">
                    <li class="un_texto_pequenio">
                      <span class="" style="font-weight: bold; text-decoration: underline">
                        <a href="javascript:void(0)" v-on:click="() => (tareaDetails['está terminada'] ? undoTarea(tareaDetails.id) : doTarea(tareaDetails.id))">Está terminada:</a>
                      </span>
                      <span class=""> {{ tareaDetails["está terminada"] ? "sí" : "no" }}</span>
                    </li>
                    <li class="un_texto_pequenio">
                      <span class="" style="font-weight: bold; text-decoration: underline">Actividades asociadas:</span>
                      <span class=""> {{ tareaDetails.actividades.length }}</span>
                    </li>
                    <li class="un_texto_pequenio">
                      <span class="" style="font-weight: bold; text-decoration: underline">Fecha:</span>
                      <span class=""> {{ tareaDetails["fecha de inicio"] }}</span>
                    </li>
                    <li class="un_texto_pequenio">
                      <span class="" style="font-weight: bold; text-decoration: underline">Descripción:</span>
                      <span class=""> {{ tareaDetails["descripción"] || "sin descripción" }}</span>
                    </li>
                    <li class="un_texto_pequenio">
                      <span class="" style="font-weight: bold; text-decoration: underline">Detalles:</span>
                      <span class=""> {{ tareaDetails.detalles || "sin detalles" }}</span>
                    </li>
                  </ul>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="" v-else-if="internal_selected_option === 'por horas'" v-bind:key="'agenda-vista-por-horas'">
        <!--h3 class="texto_importante_1">por horas</h3-->
        <div class="un_espaciado">
          <div class="un_texto_pequenio" v-if="typeof internal_tareas_per_hour === 'undefined'">Cargando tareas del día... Por favor, espere...</div>
          <table class="una_tabla la_tabla_de_la_agenda" v-else>
            <tbody v-for="(hourTasks, hourIndex) in internal_tareas_per_hour" v-bind:key="'agenda-vista-por-horas-hora-' + hourIndex">
              <tr v-if="isNowHour(hourIndex)">
                <td class="" colspan="100">
                  <div class="un_texto_pequenio el_contenedor_de_la_barra_del_ahora" style="">
                    <div class="un_texto_pequenio la_barra_del_ahora">...ahora son las {{ $utils.formatHourAmPmFromDate(internal_now_date) }}...</div>
                  </div>
                </td>
              </tr>
              <tr>
                <td class="la_celda_del_texto_de_la_hora_de_la_agenda" v-on:click="() => goToAddTarea(hourIndex)">
                  <span class="un_texto_pequenio el_texto_de_la_hora_de_la_agenda">+{{ hourIndex }}</span>
                </td>
                <td class="ancho_maximo" style="vertical-align: top; border-top: 1px dashed #333; padding-top: 1px">
                  <div class="un_texto_pequenio el_texto_de_la_tarea_de_la_agenda" v-if="hourTasks && hourTasks.length" v-on:click="toggleSelectedHourByIndex(hourIndex)" style="padding">{{ hourTasks.length }} tarea{{ hourTasks.length === 1 ? "" : "s" }}.</div>
                </td>
              </tr>
              <template v-if="internal_selected_hours.indexOf(hourIndex) !== -1">
                <tr v-for="(taskData, taskIndex) in hourTasks" v-bind:key="'agenda-vista-por-horas-hora-' + hourIndex + '-task-' + taskIndex">
                  <td class="un_texto_pequenio" style="vertical-align: middle" colspan="10">
                    <div class="un_espaciado">
                      <span v-if="taskData['está terminada']" style="white-space: nowrap">✔</span>
                      <span v-else style="white-space: nowrap">✖</span>
                      <span style="padding-left: 4px">{{ taskData.nombre }}. </span>
                      <span class="un_texto_muy_pequenio">
                        <span v-if="taskData['está terminada']" style="white-space: nowrap">
                          <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.undoTarea(taskData.id).then(() => refreshTargetDate())"> Deshacer </a>
                        </span>
                        <span v-else>
                          <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.doTarea(taskData.id).then(() => refreshTargetDate())"> Hacer </a>
                        </span>
                        <span> | </span>
                        <span>
                          <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => goToTarea(taskData)"> Editar </a>
                        </span>
                        <span> | </span>
                        <span style="white-space: nowrap">
                          <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.deleteTarea(taskData.id) || refreshTargetDate()"> Eliminar </a>
                        </span>
                        <span> tarea#{{ taskData.id }}</span>
                        <span>.</span>
                      </span>
                    </div>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginaAgendaVerAgenda",
  props: {
    root: {
      type: Object,
      required: true,
    },
    pageProperties: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      internal_selected_option: this.$utils.getPreference("preferences.agenda.view", "por tareas"),
      internal_selected_hours: [],
      internal_now_date: new Date(),
      internal_error: undefined,
      internal_target_date: undefined,
      internal_tareas: undefined,
      internal_tareas_per_hour: undefined,
      internal_tareas_per_tarea: undefined,
    };
  },
  methods: {
    goToAddTarea(horaDeTarea = "00:00") {
      const [ horaTareaStr, minutoTareaStr ] = horaDeTarea.split(":");
      const horaTarea = parseInt(horaTareaStr);
      const minutoTarea = parseInt(minutoTareaStr);
      const targetDate = new Date(this.internal_target_date);
      targetDate.setHours(horaTarea);
      targetDate.setMinutes(minutoTarea);
      targetDate.setSeconds(0);
      targetDate.setMilliseconds(0);
      const targetDateString = this.$utils.getStringFromDate(targetDate);
      console.log(targetDateString);
      this.root.goToPage("PaginaAgendaAniadirTarea", {
        value: {
          "fecha de inicio": targetDateString,
        },
      });
    },
    goToTarea(tarea) {
      this.root.goToPage("PaginaAgendaEditarTarea", { value: tarea });
    },
    goToPreviousDay() {
      const targetDate = new Date(this.internal_target_date);
      targetDate.setDate(targetDate.getDate() - 1);
      this.root.goToPage("PaginaAgendaVerAgenda", { targetDate });
    },
    goToNextDay() {
      const targetDate = new Date(this.internal_target_date);
      targetDate.setDate(targetDate.getDate() + 1);
      this.root.goToPage("PaginaAgendaVerAgenda", { targetDate });
    },
    toggleSelectedHourByIndex(hourIndex) {
      const hourPos = this.internal_selected_hours.indexOf(hourIndex);
      if (hourPos !== -1) {
        this.internal_selected_hours.splice(hourPos, 1);
      } else {
        this.internal_selected_hours.push(hourIndex);
      }
    },
    refreshTargetDateTareasPerHour() {
      const tareas_per_hour = {};
      for (let index = 0; index < 24; index++) {
        const currentHour = this.$utils.padStart("" + index, 2, "0") + ":00";
        tareas_per_hour[currentHour] = [];
        this.internal_tareas.forEach((tarea) => {
          const fechaDeInicio = tarea["fecha de inicio"];
          const dateDeInicio = this.$utils.getDateFromString(fechaDeInicio);
          if (dateDeInicio.getHours() === index) {
            tareas_per_hour[currentHour].push(tarea);
          }
        });
      }
      this.internal_tareas_per_hour = tareas_per_hour;
    },
    refreshTargetDateTareasPerTarea() {
      const tareas_per_tarea = {};
      this.internal_tareas.sort((a, b) => {
        if(a["está terminada"] && b["está terminada"]) {
          return -1;
        } else if(a["está terminada"] && (!b["está terminada"])) {
          return -1;
        } else if((!a["está terminada"]) && b["está terminada"]) {
          return 1;
        } else if((!a["está terminada"]) && (!b["está terminada"])) {
          return -1;
        }
        return -1;
      }).forEach((tarea) => {
        tareas_per_tarea[tarea.id] = tarea;
      });
      this.internal_tareas_per_tarea = tareas_per_tarea;
    },
    async refreshTargetDate() {
      try {
        const currentDate = this.internal_target_date;
        this.internal_tareas = [];
        this.internal_tareas = await this.$db.select("tarea", {
          filter: (v) => {
            try {
              const startTime = v["fecha de inicio"] || undefined;
              const startDate = this.$utils.getDateFromString(startTime);
              if (startDate.getFullYear() === currentDate.getFullYear() && startDate.getMonth() === currentDate.getMonth() && startDate.getDate() === currentDate.getDate()) {
                return true;
              }
            } catch (error) {
              console.log("nada...");
              return false;
            }
          },
        });
        this.refreshTargetDateTareasPerHour();
        this.refreshTargetDateTareasPerTarea();
      } catch (error) {
        this.internal_error = error;
      }
    },
    isNowHour(hourText) {
      const isSameDate = this.internal_target_date.getDate() === this.internal_now_date.getDate() && this.internal_target_date.getMonth() === this.internal_now_date.getMonth() && this.internal_target_date.getFullYear() === this.internal_now_date.getFullYear();
      if (!isSameDate) {
        return false;
      }
      return this.internal_now_date.getHours() === parseInt(hourText.split(":")[0]);
    },
  },
  watch: {
    internal_target_date(v) {
      this.$noop(v);
      this.refreshTargetDate();
    },
    internal_selected_option(v) {
      this.$utils.setPreference("preferences.agenda.view", v);
    },
  },
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_target_date = this.pageProperties && this.pageProperties.targetDate ? this.pageProperties.targetDate : new Date();
    this.refreshTargetDate();
  },
  updated() {},
  destroyed() {},
};
</script>
<style>
.Component-PaginaAgendaVerAgenda {
}
</style>
