      <h3 class="texto_importante_1">1. Secciones</h3>
      <p>Las secciones de la aplicación son las que aparecen dándole al botón de arriba a la izquierda. [...]</p>
      <h3 class="texto_importante_1">2. General</h3>
      <p><b>«Mind and Body Control»</b> es una simple aplicación web especialmente pensada para monitorizar aspectos de uno mismo <b>de forma completamente local y fuera de línea</b>, es decir, que la aplicación no funciona con internet en ningún momento, y tus datos no salen en ningún momento de tu dispositivo, sea PC, tablet, móvil o cualquier otro.</p>
      <h3 class="texto_importante_1">3. Instalación completa</h3>
      <p>La actual aplicación no requiere de instalación para que funcione correctamente: solo no borrar la memoria caché. Sin embargo, puede instalarse completamente en el dispositivo simplemente dejando un icono en el escritorio.</p>
      <p>Las ventajas de descargar la aplicación consiste en que cuando no tengas absolutamente nada de internet, pese a todo, funcione. Para ello, solo tienes que darle al botón de los 3 puntos o rayas del navegador, y buscar la opción "Añadir a la pantalla de inicio" o su equivalente en el idioma que se tenga configurado.</p>
      <p>Este tipo de instalación depende de la tecnología de las <a href="#">Progressive Web Apps (o PWA)</a>, por lo cual, que funcione en tu dispositivo depende del soporte que dé de esta tecnología el navegador que usas (Firefox, Google Chrome... y luego versión móvil, escritorio...). Puedes consultar el soporte de todas las tecnologías de tu navegador en internet.</p>
      <p></p>
      <h3 class="texto_importante_1">4. Propósito</h3>
      <p>El propósito de esta aplicación es convertirse en un acompañante del usuario con el fin de obtener un mayor conocimiento del uso de la mente y del cuerpo propios, monitorizando algunos aspectos del rastro de estos dos, como son: tareas, actividades, objetivos, comidas, productos, componentes, efectos. También ofrece una sección de listas, y otra de notas. Finalmente, una sección de consola, donde se pueden ejecutar scripts más especializados desde la misma perspectiva del usuario.</p>
      <p>Una de las razones por la que se hace esta aplicación también es para asegurar que los datos no viajan en ningún momento a través de internet y hacia ningún lugar: los datos de la aplicación son datos cacheados por el navegador web, y ninguna funcionalidad extra requiere de conexión a internet. Eso sí: los datos dependen directamente del <b>dispositivo</b> y también del <b>navegador web</b>. Es por esto que la aplicación <b>no guardará los datos</b> si se utiliza el modo incógnito, o si se abre con otro navegador distinto a la anterior vez. Por tanto, para acumular los datos, hay que usar el mismo dispositivo y el mismo navegador.</p>
      <p>También se puede encontrar la sección de <a href="#" v-on:click="root.goToPage('PaginaConfiguraciones')">«Configuraciones»</a>, en la que se pueden cambiar cosas como el nombre de usuario.</p>
      <p>Además de estas secciones descritas, hay otras secciones que pueden ser de interés, pero que se dejarán a merced del usuario explorar, y están marcadas con un color de fondo distinto.</p>
      <h3 class="texto_importante_1">5. Funcionalidades</h3>
      <p>Con esta aplicación, uno puede buscar, añadir, cambiar o eliminar los siguientes elementos:</p>
      <ul class="un_espaciado">
        <li>
          <p>
            <u>1. Tareas (en «Agenda»):</u><span> reflejan las <u>actividades</u> que se quedan por realizar o que se han realizado.</span>
          </p>
        </li>
        <li>
          <p>
            <u>2. Actividades (en «Actividad»):</u><span> reflejan las posibles <u>actividades</u> predefinidas, que persiguen o tienen <u>objetivos</u> asociadas consigo.</span>
          </p>
        </li>
        <li>
          <p>
            <u>3. Objetivos (en «Actividad»):</u><span> reflejan las metas de las <u>actividades</u>.</span>
          </p>
        </li>
        <li>
          <p>
            <u>4. Comida (en «Alimentación»):</u><span> reflejan las ingestas <u>productos</u> realizadas.</span>
          </p>
        </li>
        <li>
          <p>
            <u>5. Producto (en «Alimentación»):</u><span> reflejan los productos alimenticios que pueden tomarse en cada <u>comida</u>. Al mismo tiempo, un producto arrastra unos <u>componentes</u> más pequeños, así como <u>efectos</u> propios del producto como combinación de estos componentes menores.</span>
          </p>
        </li>
        <li>
          <p>
            <u>6. Componente (en «Alimentación»):</u><span>  reflejan las sustancias más pequeñas que pueden encontrarse en un <b>producto</b> dado, así como arrastrarse unos <b>efectos</b> de dicho componente.</span>
          </p>
        </li>
        <li>
          <p>
            <u>7. Efecto (en «Alimentación»):</u><span> reflejan los distintos tipos de <u>efectos</u> que pueden producir los <u>componentes</u> o los <u>productos</u> sobre el cuerpo humano.</span>
          </p>
        </li>
        <li>
