            <u>9. Notas (en «Notas»):</u><span> reflejan simples notas escritas.</span>
          </p>
        </li>
      </ul>
      <h3 class="texto_importante_1">6. El buscador</h3>
      <div class="un_espaciado">
        <div>El buscador, que es la barra que aparece encima de las tablas de este tipo de elementos previamente listados, es un elemento algo especial, pero que puede resultar muy útil saberlo manejar. Este buscador, que aparece en múltiples sitios del aplicativo, te permite hacer varias cosas con un mismo texto de búsqueda. Las acciones que te permite hacer este buscador especial son, por este orden:</div>
        <ul>
          <li><u>1. Buscar texto.</u> La primera parte del filtro, sin que haya aparecido el caracter <b>&amp;</b>, es para el texto a buscar.</li>
          <li><u>2. Aplicar filtros.</u> A partir del primer <b>&amp;</b>, se aplican filtros, siempre que se aplique la sintaxis de filtro: {columna}{operación lógica}{parámetros}. Aquí, en {operación lógica}, podemos encontrar: &gt;, &ge;, &lt;, &le;, =, !=, in, not in, has, has not, like, not like,... Los filtros son acumulativos, por lo cual los resultados devueltos son los únicos que cumplen todas las reglas de filtro, no solo una. Para aplicar muchos filtros, júntelos con el signo &amp;. Para los operadores «in» y «not in», utilice comas (,) para separar distintos valores en el parámetro final del filtro.</li>
          <li><u>3. Ordenar por columnas.</u> A partir del primer <b>&amp;</b>, y después de todos los filtros, se aplican reglas de ordenación en la búsqueda. Esta operación tiene la sintaxis: +columna1,-columna2,+columna3. Cuando el signo que precede a la columna se omite o es +, el orden será ascendente. Cuando el signo es -, el orden será descendente. Las columnas guardarán la prioridad en la que están a la hora de ordenar los resultados.</li>
        </ul>
        <p>De esta forma, buscar las <u>tareas</u> que contengan el texto <u>bici</u> y que hayan sido programadas para antes del <u>25 de diciembre del 2022</u> (25/12/2022), y que además se nos listen ordenadas de forma <u>descendiente</u> por el nombre primero y luego por la fecha para la que está programada, se escribiría así en el buscador de <u>tareas</u>:</p>
        <p style="text-align: center">bici&amp;"fecha de inicio"&lt;2022-12-25&amp;-nombre,-"fecha de inicio"</p>
        <p>Eso es toda la explicación que la aplicación debería necesitar, más o menos. Recuerda que se pueden aplicar distintos filtros y reglas de ordenación combinados al mismo tiempo, y que si omites algún parámetro (de los 3 principales: búsqueda, filtro y ordenación), la aplicación intentará encontrar el patrón de la siguiente. Fácil.</p>
        <p>¡Ah! Y las columnas en el filtrado estan escritas en minúsculas. "Descripción" es "descripción" en los filtros, por ejemplo. Los saltos se respetan, y se consideran caracteres especiales junto con acentos y otros, lo que me recuerda que...</p>
        <p>¡Ah! Y para referirse a columnas con caracteres especiales (como acentos, espacios, etc.) puedes utilizar comitas dobles: "nombre de columna", "descripción" o "fecha de inicio".</p>
        <p>¡Ah! Y el formato de las fechas es: "YYYY-MM-DD HH:mm". Hasta ahí, más o menos todo.</p>
        <p>¡Ah! Y para los datos booleanos, como el "está terminada" de tareas, los operadores en el filtrado son literalmente: "true" y "false".</p>
        <p>¿OK? Si tienes dudas puedes probar de usarlo, solo tienes que escribir.</p>
      </div>
      <h3 class="texto_importante_1">7. Garantía de privacidad</h3>
      <p>Este software ofrece garantía de la absoluta privacidad individual de los datos, y por ello, esta aplicación ni necesita ni hace uso, en ningún momento real o hipotético, de la comunicación con internet, ni con la red local del dispositivo en el que se ejecuta. Los datos del usuario quedan guardados secretamente en la caché de la aplicación o navegador que lo visualiza, y no se comparte con otras aplicaciones tampoco. Es pues, una garantía de privacidad de los datos para el usuario.</p>
      <p>La consola, no obstante, es un ejemplo donde, en caso de utilizar scripts de terceros autores, sí podría algún autor utilizar tus datos, por lo cual son puntos sensibles de la aplicación donde hay que ir con cuidado.</p>
