<template>
  <div class="Component-ControlDeBinario">
    <div class="una_etiqueta">{{ internal_label }}</div>
    <label class="el_boton_del_control_binario">
        <input class="un_control un_control_binario" type="checkbox" v-model="internal_value" />
        <span class="el_mensaje_de_checkbox" v-if="internal_value">{{ checkedMessage }}</span>
        <span class="el_mensaje_de_checkbox" v-else>{{ uncheckedMessage }}</span>
    </label>
  </div>
</template>
<script>

export default {
    name: "ControlDeBinario",
    props: {
        root: {
            type: Object,
            required: true,
        },
        label: {
            type: String,
            required: false,
        },
        value: {
            type: Boolean,
            default: () => false,
        },
        checkedMessage: {
            type: String,
            required: false,
            default: () => "Sí",
        },
        uncheckedMessage: {
            type: String,
            required: false,
            default: () => "No",
        },
        onChange: {
            type: Function,
            required: false,
            default: () => () => undefined
        },
    },
    data() {
        return {
            internal_label: this.label,
            internal_value: this.value,
            internal_on_change: this.onChange,
        };
    },
    methods: {},
    watch: {
        internal_value(v) {
            this.internal_on_change(v, this);
        }
    },
    computed: {},
    beforeCreate() {},
    beforeMount() {},
    beforeUpdate() {},
    beforeDestroy() {},
    created() {},
    mounted() {
        this.internal_on_change(this.internal_value, this);
    },
    updated() {},
    destroyed() {},
};

</script>
