export default {
    credentials,
    schema,
    getDefaultValue(table) {
        const allColumns = schema.modelos[table].columnas;
        const allColumnIds = Object.keys(allColumns);
        return allColumnIds.reduce((output, id) => {
            const columnType = allColumns[id];
            if (columnType === "string") {
                output[id] = "";
            } else if (columnType === "text") {
                output[id] = "";
            } else if (columnType === "integer") {
                output[id] = 0;
            } else if (columnType === "number") {
                output[id] = 0;
            } else {
                output[id] = null;
            }
            return output;
        }, {})
    },
    getNextIdFor(table, data) {
        if (!(table in data)) {
            data[table] = [];
        }
        if (!("$$$_INTERNAL_DATA" in data)) {
            data.$$$_INTERNAL_DATA = {};
        }
        if (!(table in data.$$$_INTERNAL_DATA)) {
            data.$$$_INTERNAL_DATA[table] = { lastId: 0 };
        }
        return ++data.$$$_INTERNAL_DATA[table].lastId;
    },
    getRootStorage() {
        const jsonData = localStorage[credentials.storageID] || '{"db":{},"settings":{}}';
        return JSON.parse(jsonData);
    },
    getSettings() {
        return this.getRootStorage().settings;
    },
    setSettings(settings) {
        const allData = this.getRootStorage();
        allData.settings = Object.assign({}, allData.settings, settings);
        localStorage[credentials.storageID] = JSON.stringify(allData);
        return true;
    },
    getDatabase(table = undefined) {
        const allData = this.getRootStorage();
        const data = allData.db;
        if (table && (!(table in data))) {
            data[table] = [];
        }
        return data;
    },
    setDatabase(data) {
        const allData = this.getRootStorage();
        allData.db = data;
        localStorage[credentials.storageID] = JSON.stringify(allData);
        return true;
    },
    selectSync(table, options = {}) {
        const data = this.getDatabase(table);
        if (options.filter) {
            return data[table].filter(options.filter);
        }
        return data[table];
    },
    async select(table, options = {}) {
        const data = this.getDatabase(table);
        if (options.filter) {
            return data[table].filter(options.filter);
        }
        return data[table];
    },
    async insert(table, valueP) {
        const data = this.getDatabase(table);
        const value = Object.assign({}, valueP);
        if (["string", "undefined"].indexOf(typeof value.nombre) !== -1) {
            const repes = data[table].filter(row => row.nombre === value.nombre);
            if (repes.length !== 0) {
                throw new Error("La inserción de datos falló debido a una repetición de fila para «" + table + "» con «nombre» «" + value.nombre + "».");
            }
        }
        const id = this.getNextIdFor(table, data);
        if ("id" in value) delete value.id;
        const finalValue = { id, ...this.getDefaultValue(table), ...value }
        data[table].push(finalValue);
        this.setDatabase(data);
        return id;
    },
    async update(table, id, valueP) {
        try {
            const data = this.getDatabase(table);
            const value = Object.assign({}, valueP);
            const selectedIds = data[table].reduce((output, item, index) => {
                const isSameId = (""+item.id) === (""+id);
                if (isSameId) {
                    output.push(index);
                }
                return output;
            }, []);
            if (selectedIds.length === 1) {
                data[table][selectedIds[0]] = Object.assign({}, data[table][selectedIds[0]], value);
            } else if (selectedIds.length === 0) {
                throw new Error("Not found item «" + table + ":" + id + "» [00040460]");
            } else {
                throw new Error("Duplicated item «" + table + ":" + id + "» [00040461]");
            }
            return this.setDatabase(data);
        } catch (error) {
            console.error(error);
            throw error;
        }
    },
    async delete(table, id) {
        try {
            const data = this.getDatabase(table);
            const dataInTable = data[table];
            const selectedIndexes = dataInTable.reduce((out, item, index) => {
                if (item.id === id) {
                    out.push(index);
                }
                return out;
            }, []);
            if (selectedIndexes.length === 0) {
                throw new Error("[In DELETE] Database item «" + table + ":" + id + "» was not found [0008593]");
            } else if (selectedIndexes.length !== 1) {
                throw new Error("[In DELETE] Database item «" + table + ":" + id + "» is suposed to be unique but it is unusually concurred " + selectedIndexes.length + " times [0006859]");
            }
            data[table].splice(selectedIndexes[0], 1);
            return this.setDatabase(data);
        } catch (error) {
            console.error(error);
            throw error;
        }
    },
    undoTarea(tareaId) {
        return this
            .update("tarea", tareaId, { "está terminada": false })
            .catch((error) => {
                console.error(error);
                throw error;
            });
    },
    doTarea(tareaId) {
        return this
            .update("tarea", tareaId, { "está terminada": true })
            .catch((error) => {
                console.error(error);
                throw error;
            });
    },
    deleteTarea(tareaId) {
        if (!window.confirm("¿Está seguro de eliminar la tarea #" + tareaId + "?")) {
            return;
        }
        return this
            .delete("tarea", tareaId)
            .catch((error) => {
                console.error(error);
                throw error;
            });
    },
}