
<template>
  <div class="Component-PaginaConsola">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Consola</h3>
    <div class="un_espaciado un_texto_pequenio" v-if="Array.isArray(available_scripts)">
      <ControlDeSelector
        :root="root"
        :label="'Selección de script predefinido:'"
        :value="''"
        :on-change="v => internal_console_input = v"
        value-column="contenido"
        description-column="nombre"
        :options="available_scripts" />
    </div>
    <div class="un_espaciado_sin_arriba un_texto_pequenio">
      <textarea ref="console_input_element" class="un_control el_texto_de_entrada_de_consola" placeholder="Entrada de consola" v-model="internal_console_input"></textarea>
      <button class="un_control" v-on:click="executeCode">Ejecutar código</button>
    </div>
    <div class="un_espaciado_sin_arriba un_texto_pequenio">
      <textarea class="un_control el_texto_de_salida_de_consola" placeholder="Salida de consola" v-model="internal_console_output"></textarea>
    </div>
    <div class="un_espaciado_sin_arriba">
      <div ref="injectable_console_element" id="injectable_console_element"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginaConsola",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      available_scripts: false,
      internal_console_input: "",
      internal_console_output: "",
    };
  },
  methods: {
    async executeCode() {
      try {
        const input = this.internal_console_input;
        window.__MnBC_global_injector__ = {
          $textareaComponent: this.$refs.console_input_element,
          $rootComponent: this.root,
          $injectableElement: this.$refs.injectable_console_element,
        };
        const output = await window.eval(`

(async function() {
  try {
    const $textarea = window.__MnBC_global_injector__.$textareaComponent;
    const $root = window.__MnBC_global_injector__.$rootComponent;
    const $injectableHTMLElement = window.__MnBC_global_injector__.$injectableElement;
    ${input}
  } catch(error) {
    console.error("Error with eval:", error);
    throw error;
  }
}).call(this);

`);
        try {
          this.internal_console_output = JSON.stringify(output, null, 4);
        } catch(error) {
          this.internal_console_output = error;
        }
      } catch (error) {
        this.internal_console_output = "Error: " + JSON.stringify(error);
      }
    },
  },
  watch: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  async mounted() {
    const dbScripts = await this.$db.select("script", {
      filter: v => this.$noop(v) || true
    });
    this.available_scripts = [{nombre:"",contenido:""}].concat(dbScripts);
  },
  updated() {},
  destroyed() {},
};
</script>
<style>
.Component-PaginaConsola {
}
</style>
