<template>
  <div class="Component-ControlDeSelector">
    <label class="una_etiqueta">{{ internal_label }}</label>
    <select class="un_control" :title="internal_placeholder" v-model="internal_value">
      <template v-for="(option, optionIndex) in options">
        <template v-if="Array.isArray(option)">
          <option :value="option" v-bind:key="'control-de-selector-' + uniqueId + '-opcion-' + optionIndex">
            {{ option }}
          </option>
        </template>
        <template v-else-if="typeof option === 'object'">
          <option :value="valueColumn ? option[valueColumn] : option" v-bind:key="'control-de-selector-' + uniqueId + '-opcion-' + optionIndex">
            {{ option[descriptionColumn] }}
          </option>
        </template>
        <template v-else-if="typeof option === 'string'">
          <option :value="option" v-bind:key="'control-de-selector-' + uniqueId + '-opcion-' + optionIndex">
            {{ option }}
          </option>
        </template>
      </template>
    </select>
  </div>
</template>
<script>
export default {
  name: "ControlDeSelector",
  props: {
    root: {
      type: Object,
      required: true,
    },
    uniqueId: {
      type: String,
      default: () => "",
    },
    label: {
      type: String,
      required: false,
    },
    value: {
      type: String,
      required: false,
    },
    valueColumn: {
      type: [String, undefined],
      default: () => undefined,
    },
    descriptionColumn: {
      type: String,
      default: () => "nombre",
    },
    options: {
      type: Array,
      required: true,
    },
    onChange: {
      type: Function,
      required: false,
      default: () => () => undefined,
    },
  },
  data() {
    return {
      internal_label: this.label,
      internal_value: this.value,
      internal_placeholder: this.placeholder,
      internal_on_change: this.onChange,
    };
  },
  methods: {},
  watch: {
    internal_value(v) {
      this.internal_on_change(v, this);
    },
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_on_change(this.internal_value, this);
  },
  updated() {},
  destroyed() {},
};
</script>
