import SearchParser from "./assets/scripts/searcher.es6.js";
const ALPHABET = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123465789".split("");
const ALL_TRANSLATIONS = {
    "esta terminada": "Está terminada",
};

export default {
    ALL_MONTHS: {
        0: "Enero",
        1: "Febrero",
        2: "Marzo",
        3: "Abril",
        4: "Mayo",
        5: "Junio",
        6: "Julio",
        7: "Agosto",
        8: "Septiembre",
        9: "Octubre",
        10: "Noviembre",
        11: "Diciembre",
    },
    ALL_DAYS: {
        0: "Domingo",
        1: "Lunes",
        2: "Martes",
        3: "Miércoles",
        4: "Jueves",
        5: "Viernes",
        6: "Sábado",
    },
    $noop() {},
    padStart(textP, len, ch = '0') {
        this.$noop(textP, len, ch);
        const text = "" + textP;
        const curLen = text.length;
        const maxLen = len;
        if(curLen >= maxLen) {
            return text;
        }
        let out = "";
        for(let i = curLen; i < maxLen; i++) {
            out += ch;
        }
        out += text;
        return out;
    },
    capitalize(text) {
        return text.substr(0,1).toUpperCase() + text.substr(1);
    },
    capitalizeAndHumanize(text) {
        const humanized = this.humanize(text);
        return humanized.substr(0, 1).toUpperCase() + humanized.substr(1);
    },
    humanize(text) {
        if(text in ALL_TRANSLATIONS) {
            return ALL_TRANSLATIONS[text];
        }
        return this.fromCamelToCapital(text);
    },
    fromHumanToCapital(text) {
        return text
            .replace("á", "a")
            .replace("é", "e")
            .replace("í", "i")
            .replace("ó", "o")
            .replace("ú", "u")
            .replace(/ [a-z]/g, m => m.trim().toUpperCase());
    },
    fromCamelToCapital(text) {
        return text
            .substr(0,1)
            .toUpperCase()
            + 
            text
            .substr(1)
            .replace(/_+([a-z])/g, (m, m1) => " " + m1);
    },
    parseSearch(text) {
        return SearchParser.parse(text);
    },
    getRandomString(len, alphabet = ALPHABET) {
        let out = "";
        for(let index = 0; index < len; index++) {
            const char = alphabet[Math.floor(Math.random() * alphabet.length)];
            out += char;
        }
        return out;
    },
    getAddPageFromModel(model) {
        if (model === "actividad") {
            return "PaginaActividadAniadirActividad";
        } else if (model === "objetivo") {
            return "PaginaActividadAniadirObjetivo";
        } else if (model === "prototipo_de_actividad") {
            return "PaginaActividadAniadirPrototipo";
        } else if (model === "tarea") {
            return "PaginaAgendaAniadirTarea";
        } else if (model === "comida") {
            return "PaginaAlimentacionAniadirComida";
        } else if (model === "componente") {
            return "PaginaAlimentacionAniadirComponente";
        } else if (model === "efecto") {
            return "PaginaAlimentacionAniadirEfecto";
        } else if (model === "producto") {
            return "PaginaAlimentacionAniadirProducto";
        } else if (model === "lista") {
            return "PaginaListasAniadirLista";
        } else if (model === "nota") {
            return "PaginaNotasAniadirNota";
        } else if (model === "script") {
            return "PaginaScriptsAniadirScript";
        } else if (model === "esquema") {
            return "PaginaEsquemasAniadirEsquema";
        } else if (model === "tarea_predefinida") {
            return "PaginaActividadAniadirTareaPredefinida";
        } else {
            throw new Error("Error on $utils.getAddPageFromModel(): unidentified model «" + model + "»");
        }
    },
    getEditPageFromModel(model) {
        if (model === "actividad") {
            return "PaginaActividadEditarActividad";
        } else if (model === "objetivo") {
            return "PaginaActividadEditarObjetivo";
        } else if (model === "prototipo_de_actividad") {
            return "PaginaActividadEditarPrototipo";
        } else if (model === "tarea") {
            return "PaginaAgendaEditarTarea";
        } else if (model === "comida") {
            return "PaginaAlimentacionEditarComida";
        } else if (model === "componente") {
            return "PaginaAlimentacionEditarComponente";
        } else if (model === "efecto") {
            return "PaginaAlimentacionEditarEfecto";
        } else if (model === "producto") {
            return "PaginaAlimentacionEditarProducto";
        } else if (model === "lista") {
            return "PaginaListasEditarLista";
        } else if (model === "nota") {
            return "PaginaNotasEditarNota";
        } else if (model === "script") {
            return "PaginaScriptsEditarScript";
        } else if (model === "esquema") {
            return "PaginaEsquemasEditarEsquema";
        } else if (model === "tarea_predefinida") {
            return "PaginaActividadEditarTareaPredefinida";
        } else {
            throw new Error("Error on $utils.getEditPageFromModel(): unidentified model «" + model + "»");
        }
    },
    getDateForFiles(currentDate) {
        return `${this.padStart(currentDate.getFullYear(), 2, '0')
            }-${this.padStart(currentDate.getMonth() + 1, 2, '0')
            }-${this.padStart(currentDate.getDate(), 2, '0')
            }T${this.padStart(currentDate.getHours(), 2, '0')
            }-${this.padStart(currentDate.getMinutes(), 2, '0')
            }-${this.padStart(currentDate.getSeconds(), 2, '0')
        }`;
    },
    getStringFromDate(currentDate = new Date()) {
        return `${this.padStart(currentDate.getFullYear(), 2, '0')
            }-${this.padStart(currentDate.getMonth() + 1, 2, '0')
            }-${this.padStart(currentDate.getDate(), 2, '0')
            } ${this.padStart(currentDate.getHours(), 2, '0')
            }:${this.padStart(currentDate.getMinutes(), 2, '0')
            }`;
    },
    getStringInversedFromDate(currentDate = new Date()) {
        const stringDate = this.getStringFromDate(currentDate);
        const [dayOfYear, hour] = stringDate.split(" ");
        const [year, month, day] = dayOfYear.split("-");
        return `${hour} ${day}-${month}-${year}`;
    },
    getStringWithMinutesFromDate(currentDate) {
        if (!currentDate) return "";
        if (isNaN(currentDate)) return "";
        const weekday = this.ALL_DAYS[""+currentDate.getDay()].substr(0, 3).toUpperCase();
        const inversedDate = this.getStringInversedFromDate(currentDate)
            .replace(/ /g, " " + weekday + " ")
            .replace(/-/g, "/")
            .replace(/[0-9][0-9]\/[0-9][0-9][0-9][0-9]$/g, match => {
                return match
                    .replace(/^[0-9][0-9]/g, (month) => {
                        return this.ALL_MONTHS[parseInt(month) - 1].substr(0, 3).toUpperCase();
                    });
            });
        const currentHour = parseInt(inversedDate.match(/^[0-9][0-9]/g)[0])
        const particleAmPm = currentHour < 12 ? "AM" : "PM";
        const result = inversedDate.substr(0, 5) + particleAmPm + inversedDate.substr(5);
        return result.toLowerCase();
    },
    getStringWithWeekdayFromDate(currentDate = new Date()) {
        return this.getStringWithMinutesFromDate(currentDate).substr(8);
    },
    getPoliteStringFromDate(currentDate) {
        return `${
            this.ALL_DAYS[currentDate.getDay()]
        }, ${
            this.padStart(currentDate.getDate(), 2, '0')
        } de ${
            this.ALL_MONTHS[currentDate.getMonth()]
        } del ${
            this.padStart(currentDate.getFullYear(), 2, '0')
        }`;
    },
    getDateFromString(minuteString) {
        const dateParts = minuteString.split(/[^0-9]+/g);
        const otherDate = new Date();
        if(dateParts[0]) {
            const datePart = parseInt(dateParts[0]);
            otherDate.setFullYear(datePart);
        }
        if (dateParts[1]) {
            const datePart = parseInt(dateParts[1]);
            otherDate.setMonth(datePart - 1);
        }
        if (dateParts[2]) {
            const datePart = parseInt(dateParts[2]);
            otherDate.setDate(datePart);
        }
        if (dateParts[3]) {
            const datePart = parseInt(dateParts[3]);
            otherDate.setHours(datePart);
        }
        if (dateParts[4]) {
            const datePart = parseInt(dateParts[4]);
            otherDate.setMinutes(datePart);
        }
        if (dateParts[5]) {
            const datePart = parseInt(dateParts[5]);
            otherDate.setSeconds(datePart);
        }
        return otherDate;
    },
    downloadLocalFile(file, contents) {
        const element = document.createElement("a");
        element.setAttribute("href", "data:text/plain;charset=utf-8," + encodeURIComponent(contents))
        element.setAttribute("download", file);
        element.style.display = "none";
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    },
    getPrettyId(id, ids) {
        const finalId = [].concat(ids).sort().filter(it => it.substr(0, 2) === id.substr(0, 2)).indexOf(id);
        if(finalId <= 0) {
            return id.substr(0,2);
        }
        return id.substr(0,2) + (finalId+1);
    },
    getAllPreferences() {
        const jsonStorage = localStorage[this.$db.credentials.storageID];
        const dataStorage = JSON.parse(jsonStorage);
        if (!("settings" in dataStorage)) {
            dataStorage.settings = {};
        }
        if(!("preferences" in dataStorage.settings)) {
            dataStorage.settings.preferences = {};
            JSON.stringify(dataStorage);
        }
        return dataStorage.settings.preferences;
    },
    setAllPreferences(data) {
        const jsonStorage = localStorage[this.$db.credentials.storageID];
        const dataStorage = JSON.parse(jsonStorage);
        if (!("settings" in dataStorage)) {
            dataStorage.settings = {};
        }
        dataStorage.settings.preferences = data;
        localStorage[this.$db.credentials.storageID] = JSON.stringify(dataStorage);
        return dataStorage.settings.preferences;
    },
    setPreference(id, defaultValue = undefined) {
        const allPreferences = this.getAllPreferences();
        allPreferences[id] = defaultValue;
        this.setAllPreferences(allPreferences);
        return defaultValue;
    },
    getPreference(id, defaultValue = undefined) {
        const allPreferences = this.getAllPreferences();
        if (id in allPreferences) {
            return allPreferences[id];
        }
        return this.setPreference(id, defaultValue);
    },
    formatHourAmPmFromDate(date) {
        const currentHours = date.getHours();
        const currentMinutes = date.getMinutes();
        if (currentHours < 12) {
            return this.padStart(currentHours, 2, "0") + ":" + this.padStart(currentMinutes, 2, "0") + "am";
        } else {
            return this.padStart(currentHours, 2, "0") + ":" + this.padStart(currentMinutes, 2, "0") + "pm";
        }
    },
    formatHourAmPmFromString(text) {
        if(typeof text === "number") {
            return text > 12 ? (text + "am") : (text + "pm");
        } else if (typeof text !== "string") {
            return text;
        } else if(text.length === 2) {
            return (parseInt(text) < 12) ? (text + "am") : (text + "pm");
        } else if(text.length === 5) {
            return (parseInt(text.split(":")[1]) < 12) ? (text + "am") : (text + "pm");
        } else if (text.length > 5) {
            return (parseInt(text.split(" ")[1].split(":")[0]) < 12) ? (text.split(" ")[1] + "am") : (text.split(" ")[1] + "pm");
        } else {
            return text;
        }
    },
    getDateWith0Minutes(date) {
        const date2 = new Date(date);
        date2.setMinutes(0);
        return date2;
    },
    reduceObjetivosFromTarea(out = [], t) {
        const actividadesDeTarea = t.actividades;
        const objetivosSumadosDeTarea = t["objetivos sumados"] || [];
        out = out.concat(objetivosSumadosDeTarea);
        const nombresDeActividad = [];
        for (let indexActividad = 0; indexActividad < actividadesDeTarea.length; indexActividad++) {
            const actividadDeTarea = actividadesDeTarea[indexActividad];
            const nombreDeActividad = actividadDeTarea.nombre;
            nombresDeActividad.push(nombreDeActividad);
        }
        const objetivosSumadosDeActividad = this.$db.selectSync("actividad", {
            filter: v => nombresDeActividad.indexOf(v.nombre) !== -1
        }).reduce((out2, a) => {
            out2 = out2.concat(a["objetivos sumados"] || []);
            return out2;
        }, []);
        out = out.concat(objetivosSumadosDeActividad);
        return out;
    },
}