<template>
  <div class="Component-IntroductorioPorMes">
    <div class="" v-if="!internal_mes"></div>
    <div class="" v-else>
      <div class="" style="margin-top: 0px">
        <div class="como_una_tabla">
          <span class="como_una_celda">
            <button class="un_control una_anchura_de_20_px" v-on:click="() => goToPreviousMonth()">«</button>
          </span>
          <span class="como_una_celda ancho_maximo texto_centrado rompible_por_espacios">{{ $utils.getPoliteStringFromDate(internal_mes).replace(/^[^ ]+ [^ ]+ [^ ]+ /g, "") }}</span>
          <span class="como_una_celda">
            <button class="un_control una_anchura_de_20_px" v-on:click="() => goToNextMonth()">»</button>
          </span>
        </div>
      </div>
      <!--div class="un_espaciado_sin_abajo la_fecha_del_introductorio">
        Mes {{ $utils.getStringWithWeekdayFromDate(internal_mes).replace(/^[^\/]+\//g, "") }}
      </div-->
    </div>
    <div class="" v-if="internal_tareas_de_mes && internal_tareas_de_mes.length">
      <div class="un_texto_pequenio como_una_tabla">
        <div class="como_una_fila">
          <span class="como_una_celda ancho_auto un_espaciado_horizontal">✔ Hechas: </span>
          <span class="como_una_celda ancho_auto"></span>
          <span class="como_una_celda ancho_maximo"><span class="un_fondo_de_completado" :style="{width: ((internal_tareas_de_mes_completadas.length / internal_tareas_de_mes_totales) * 100).toFixed(1) + '%'}">{{ internal_tareas_de_mes_completadas.length }} de {{ internal_tareas_de_mes_totales }} ({{ ((internal_tareas_de_mes_completadas.length / internal_tareas_de_mes_totales) * 100).toFixed(1) }}%)</span></span>
        </div>
        <div class="como_una_fila">
          <span class="como_una_celda ancho_auto un_espaciado_horizontal">✖ Pendientes:</span>
          <span class="como_una_celda ancho_auto"></span>
          <span class="como_una_celda ancho_maximo"><span class="un_fondo_de_fallido" :style="{width: ((internal_tareas_de_mes_fallidas.length / internal_tareas_de_mes_totales) * 100).toFixed(1) + '%'}">{{ internal_tareas_de_mes_fallidas.length }} de {{ internal_tareas_de_mes_totales }} ({{ ((internal_tareas_de_mes_fallidas.length / internal_tareas_de_mes_totales) * 100).toFixed(1) }}%)</span></span>
        </div>
        <div class="como_una_fila">
          <span class="como_una_celda ancho_auto un_espaciado_horizontal">* Totales: </span>
          <span class="como_una_celda ancho_auto"></span>
          <span class="como_una_celda ancho_maximo"><span class="un_fondo_de_total">{{ internal_tareas_de_mes_totales }} tareas para este mes</span></span>
        </div>
      </div>
      <!--div class="">
        <span class="">+ Objetivos sumados: </span>
        <span class=""><span class="un_fondo_de_total">{{ internal_tareas_de_mes_objetivos_sumados.length }} objetivos sumados para esta mes</span></span>
      </div>
      <div class="">
        <span class="">- Objetivos restados: </span>
        <span class=""><span class="un_fondo_de_total">{{ internal_tareas_de_mes_objetivos_restados.length }} objetivos restados para esta mes</span></span>
      </div-->
      <div class="un_visor_introductorio">

        <ul class="una_lista_de_bloques">
          <li class="un_espaciado_arriba" v-for="(tareaData, tareaIndex) in internal_tareas_de_mes" v-bind:key="'introductorio-por-mes-tarea-' + tareaIndex">
            <div class="un_texto_pequenio un_espaciado">
              <span class="">{{ tareaData["está terminada"] ? "✔" : "✖" }}</span>
              <span class=""> {{ $utils.getPoliteStringFromDate($utils.getDateFromString(tareaData["fecha de inicio"])) }} </span>
              <span class=""> ({{ tareaIndex+1 }}) </span>
              <span class=""> {{ tareaData.nombre }}. </span>
              <span class="un_texto_muy_pequenio un_espaciado_arriba" style="display: block; text-align: right">
                <span v-if="tareaData['está terminada']" style="white-space: nowrap">
                  <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.undoTarea(tareaData.id).then(() => refreshInternalMesData())">Deshacer</a>
                </span>
                <span v-else>
                  <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.doTarea(tareaData.id).then(() => refreshInternalMesData())">Hacer</a>
                </span>
                <span> | </span>
                <span>
                  <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => goToTarea(tareaData)">Editar</a>
                </span>
                <span> | </span>
                <span style="white-space: nowrap">
                  <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.deleteTarea(tareaData.id).then(() => refreshInternalMesData())">Eliminar</a>
                </span>
                <span> tarea#{{ tareaData.id }}</span>
                <span>.</span>
              </span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="texto_centrado el_mensaje_de_no_asignados" v-else>
      <p>No hay tareas asignadas a este mes.</p>
    </div>
    <div class="un_texto_pequenio texto_centrado">
      <button class="un_control" v-on:click="() => goToAddTarea()">Crear nueva tarea</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "IntroductorioPorMes",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      internal_mes: undefined,
      internal_tareas_de_mes: undefined,
      internal_tareas_de_mes_fallidas: undefined,
      internal_tareas_de_mes_pendientes: undefined,
      internal_tareas_de_mes_completadas: undefined,
      internal_tareas_de_mes_totales: undefined,
      internal_tareas_de_mes_objetivos_sumados: [],
      internal_tareas_de_mes_objetivos_restados: [],
      internal_error: undefined,
    };
  },
  methods: {
    goToAddTarea() {
      const currentDay = new Date(this.internal_mes);
      currentDay.setDate(1);
      currentDay.setHours(0);
      currentDay.setMinutes(0);
      this.root.goToPage("PaginaAgendaAniadirTarea", { value: { "fecha de inicio": this.$utils.getStringFromDate(currentDay) } });
    },
    goToTarea(tarea) {
      this.root.goToPage("PaginaAgendaEditarTarea", { value: tarea });
    },
    goToPreviousMonth() {
      const dateTmp = new Date(this.internal_mes);
      dateTmp.setMonth(dateTmp.getMonth() - 1);
      this.internal_mes = dateTmp;
    },
    goToNextMonth() {
      const dateTmp = new Date(this.internal_mes);
      dateTmp.setMonth(dateTmp.getMonth() + 1);
      this.internal_mes = dateTmp;
    },
    refreshInternalMesData(v = this.internal_mes) {
      if (!(v instanceof Date)) {
        this.internal_tareas_de_mes = undefined;
        return;
      }
      // const nowDate = new Date();
      // const nowDateStr = `${this.$utils.padStart(nowDate.getFullYear(), 4, "0")}-${this.$utils.padStart(nowDate.getMonth() + 1, 2, "0")}-${this.$utils.padStart(nowDate.getDate(), 2, "0")} ${this.$utils.padStart(nowDate.getHours(), 2, "0")}`;
      const startDateStr = `${this.$utils.padStart(v.getFullYear(), 4, "0")}-${this.$utils.padStart(v.getMonth() + 1, 2, "0")}`;
      this.$db
        .select("tarea", {
          filter: (item) => {
            return item["fecha de inicio"].startsWith(startDateStr);
          },
        })
        .then((dataP) => {
          const data = dataP.sort((a, b) => {
            return b["fecha de inicio"] < a["fecha de inicio"] ? 1 : 
              a["fecha de inicio"] < b["fecha de inicio"] ? -1 : 
              a["está terminada"] ? -1 : 1;
          });
          this.internal_tareas_de_mes = data;
          return data;
        })
        .catch((error) => {
          this.internal_error = error;
          throw error;
        });
    },
  },
  watch: {
    internal_mes(v) {
      this.refreshInternalMesData(v);
    },
    internal_tareas_de_mes(v) {
      if(!Array.isArray(v)) {
        this.internal_tareas_de_mes_fallidas = [];
        this.internal_tareas_de_mes_pendientes = [];
        this.internal_tareas_de_mes_completadas = [];
        this.internal_tareas_de_mes_totales = 0;
        return;
      }
      this.internal_tareas_de_mes_fallidas = v.filter((v) => {
        return !v["está terminada"];
      });
      this.internal_tareas_de_mes_pendientes = v.filter((v) => {
        this.$noop(v);
        return !v["está terminada"] && v["fecha de inicio"] > this.$utils.getStringFromDate(new Date());
      });
      this.internal_tareas_de_mes_completadas = v.filter((v) => {
        return v["está terminada"];
      });
      this.internal_tareas_de_mes_totales = v.length;
    }
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_mes = new Date();
  },
  updated() {},
  destroyed() {},
};
</script>
