<template>
  <div class="Component-PaginaConfiguraciones">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Configuraciones</h3>
    <div>
      <ul class="una_lista_de_opciones_seguidas">
        <li class="" :class="{ seleccionado: internal_selected_option === 'de usuario' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'de usuario')">De usuario</a>
        </li>
        <li class="un_separador">|</li>
        <li class="" :class="{ seleccionado: internal_selected_option === 'de proyecto' }">
          <a href="#" v-on:click="() => (internal_selected_option = 'de proyecto')">De proyecto</a>
        </li>
      </ul>
    </div>
    <div v-if="internal_selected_option === 'de usuario'" v-bind:key="'configuraciones-de-usuario'">
      <h3 class="texto_importante_1">De usuario</h3>
      <div class="un_espaciado">
        <ControlDeTexto :root="root" label="Nombre personal de usuario:" placeholder="Por defecto: Pepito Yons Smidsonian" :value="internal_user" :on-change="v => internal_user = v" />
      </div>
    </div>
    <div v-if="internal_selected_option === 'de proyecto'" v-bind:key="'configuraciones-de-proyecto'">
      <h3 class="texto_importante_1">De proyecto</h3>
      <div class="un_espaciado">
        <ControlDeTexto :root="root" label="Nombre general del proyecto:" placeholder="Por defecto: Body and mind control" :value="internal_project" :on-change="v => internal_project = v" />
      </div>
    </div>
  </div>
</template>
<script>

export default {
    name: "PaginaConfiguraciones", 
    props: {
        root: {
            type: Object,
            required: true,
        }
    },
    data() {
      const allSettings = this.$db.getSettings();
        return {
          internal_selected_option: undefined,
          internal_selected_option_2: undefined,
            internal_user: allSettings.username,
            internal_project: allSettings.project_id,
        };
    },
    methods: {},
    watch: {
        internal_user(v) {
            this.$db.setSettings({ username: v });
        },
        internal_project(v) {
            this.$db.setSettings({ project_id: v });
        },
    },
    beforeCreate() {},
    beforeMount() {},
    beforeUpdate() {},
    beforeDestroy() {},
    created() {},
    mounted() {
    },
    updated() {},
    destroyed() {},
}

</script>
