<template>
  <div class="Component-ControlDeFecha">
    <label class="una_etiqueta">{{ internal_label }}</label>
    <table class="tabla_sin_bordes ancho_maximo">
        <tbody class="">
            <tr class="">
                <td class="" style="width: 50%; text-align: center;">
                    <input class="un_control" type="date" v-model="internal_date_value" />
                </td>
                <td class="" style="width: 50%; text-align: center;">
                    <input class="un_control" type="time" v-model="internal_time_value" />
                </td>
            </tr>
        </tbody>
    </table>
  </div>
</template>
<script>

export default {
    name: "ControlDeFecha",
    props: {
        root: {
            type: Object,
            required: true,
        },
        label: {
            type: String,
            required: false,
        },
        value: {
            type: String,
            required: false,
        },
        placeholder: {
            type: String,
            required: false,
        },
        onChange: {
            type: Function,
            required: false,
            default: () => () => undefined
        },
    },
    data() {
        console.log("Loading data for ControlDeFecha:", this.value);
        return {
            internal_label: this.label,
            internal_date_value: undefined,
            internal_time_value: undefined,
            internal_value: undefined,
            internal_placeholder: this.placeholder,
            internal_on_change: this.onChange,
        };
    },
    methods: {
        
    },
    watch: {
        internal_date_value(v) {
            this.internal_value = v + " " + this.internal_time_value;
        },
        internal_time_value(v) {
            this.internal_value = this.internal_date_value + " " + v;
        },
        internal_value(v) {
            this.internal_on_change(v, this);
        }
    },
    computed: {},
    beforeCreate() {},
    beforeMount() {},
    beforeUpdate() {},
    beforeDestroy() {},
    created() {},
    mounted() {
        Object.assign(this, {
            internal_date_value: this.value ? this.value.split(" ")[0] : "",
            internal_time_value: this.value ? this.value.split(" ")[1] : "00:00",
            internal_value: this.value,
        });
    },
    updated() {},
    destroyed() {},
};

</script>
