<template>
  <div class="Component-ErrorDePagina">
    <div class="el_cuadro_del_mensaje_de_error" v-if="internal_error">
        <button class="el_boton_de_cerrar_mensaje" v-on:click="() => clearError()">
            Cerrar mensaje de error
        </button>
      <div class="el_mensaje_de_error">
        <div class="el_titulo_del_error">Error {{ internal_error_id }}</div>
        <hr class="un_separador" />
        <div class="el_tipo_del_error">
          <label class="una_negrita">Tipo de error: </label>
          <span class="">{{ internal_error.name }}</span>
        </div>
        <hr class="un_separador" />
        <div class="el_mensaje_del_error">
          <label class="una_negrita">Mensaje: </label>
          <span class="">{{ internal_error.message }}</span>
        </div>
        <hr class="un_separador" />
        <div class="la_pila_de_errores_del_error">
          <label class="una_negrita">Pila de errores: </label>
          <span class="">{{ internal_error.stack }}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ErrorDePagina",
  props: {
    root: {
      type: Object,
      required: true,
    },
    error: {
      type: [Object, Error],
      required: true,
    },
    errorId: {
      type: String,
      default: () => "",
    },
    onChange: {
      type: Function,
      default: () => "",
    },
    onClearError: {
      type: Function,
      default: () => "",
    }
  },
  data() {
    return {
      internal_error: this.error,
      internal_error_id: this.errorId,
      internal_on_clear_error: this.onClearError,
    };
  },
  methods: {
      clearError() {
        this.internal_error = undefined;
        this.internal_on_clear_error(this);
      }
    },
  watch: {
    internal_error(v) {
      this.onChange(v);
    }
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {},
  updated() {},
  destroyed() {},
};
</script>
